/* -=-=-=-=-=-=-=-=-=-= BEGIN OVERLAY =-=-=-=-=-=-=-=-=-=- */

$prefix: "overlay";
$transition-time: 0.37s;

//1px transparent images for IE8 support
$overlayBGDark: "data:image/png;base64,";
$overlayBGLight: "data:image/png;base64,";

/* Lock Page */
body.#{$prefix}-visible {
	overflow: hidden;
}

.#{$prefix}-container {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10000;
	width: 100%;
	height: 100%;
	text-align: center;

	&:before {
		content: "";
		display: inline-block;
		vertical-align: middle;
		height: 100%;
	}

	.#{$prefix}-background {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url($overlayBGDark);
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		transition: opacity $transition-time;
		-moz-transition: opacity $transition-time;
		-webkit-transition: opacity $transition-time;
		opacity: 0;
	}

	.#{$prefix}-frame {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		background-color: #f5f5f5;
		box-sizing: border-box;
		-moz-box-sizing: border-box;
		-webkit-box-sizing: border-box;
		padding: 16px;
		border-radius: 8px;
		-moz-border-radius: 8px;
		-webkit-border-radius: 8px;
		box-shadow: 0 0 16px #000000;
		-moz-box-shadow: 0 0 16px #000000;
		-webkit-box-shadow: 0 0 16px #000000;
		text-align: left;
		transition: top $transition-time, left $transition-time, transform $transition-time * 0.5;
		-moz-transition: top $transition-time, left $transition-time, transform $transition-time * 0.5;
		-webkit-transition: top $transition-time, left $transition-time, transform $transition-time * 0.5;
		outline: none;
		transform: scale(0.9);
		max-width: 90vw;
		max-height: 90vh;

		//Accessibility Section 508 and WCAG 2.0
		&:before {
			content: "Dialog Start";
			display: inline-block;
			position: absolute;
			top: -99999px;
			left: -99999px;
		}

		.#{$prefix}-content {
			outline: none;
			overflow: auto;
		}

		.#{$prefix}-close {
			position: absolute;
			width: 32px;
			height: 32px;
			right: -12px;
			top: -12px;
			background-color: transparent;
			cursor: pointer;
			text-indent: -99999px;

			&[disabled] {
				cursor: default;

				&:after {
					background-color: #888;
				}
				&:hover {
					&:after {
						background-color: #888;
					}
				}
			}

			&:after {
				content: "X";
				display: block;
				position: absolute;
				left: 50%;
				margin-left: -12px;
				top: 50%;
				margin-top: -12px;
				width: 24px;
				height: 24px;
				border-radius: 12px;
				-moz-border-radius: 12px;
				-webkit-border-radius: 12px;
				color: #fff;
				background-color: #000;
				border: 1px solid #fff;
				font-family: arial, sans-serif;
				font-weight: bold;
				line-height: 26px;
				text-indent: 0;
			}

			&:hover {
				&:after {
					background-color: #333;
				}
			}
		}
	}

	&.slide-up {
		.#{$prefix}-frame {
			top: 100%;
		}
	}
	&.slide-down {
		.#{$prefix}-frame {
			top: -100%;
		}
	}
	&.slide-right {
		.#{$prefix}-frame {
			left: -100%;
		}
	}
	&.slide-left {
		.#{$prefix}-frame {
			left: 100%;
		}
	}
	&.#{$prefix}-visible {
		.#{$prefix}-background {
			opacity: 1;
		}

		.#{$prefix}-frame {
			top: 0;
			left: 0;
			transform: scale(1);
		}
	}
}
/* -=-=-=-=-=-=-=-=-=-= END OVERLAY =-=-=-=-=-=-=-=-=-=- */
